import { NgModule } from "@angular/core";
import { RouterModule, Routes } from "@angular/router";

const routes: Routes = [
  { path: "", loadChildren: () => import("./clubs-table/clubs-table.module").then(m => m.ClubsConfigurationTableModule) },
  { path: "create", loadChildren: () => import("./create-club/create-club.module").then(m => m.CreateClubModule) },
  { path: "edit/:ClubID", loadChildren: () => import("./edit-club/edit-club.module").then(m => m.EditClubModule) }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class ClubsRoutingModule { }

@NgModule({
  imports: [ClubsRoutingModule]
})
export class ClubsModule { } 